@use 'variables' as *;

#modal_wrapper {
	z-index: 10;
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	display: none;
	background-color: $black;
	background-color: rgba(0, 0, 0, 0.5);
	overflow: auto;
	padding-top: 5%;
}
#modal {
	background-color: $white;
	margin: auto;
	width: 80%;
	height: 80%;
}
#modal_content {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	z-index: 12;
	margin: auto;
	height: 100%;
}
.modal_content_section {
	width: 50%;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}
.modal_text {
	font-size: 1.5rem;
	text-align: center;
}
.modal_image {
	width: 100%;
	height: auto;
	max-height: 100%;
}
#modal_exit {
	position: absolute;
	right: 4vw;
	top: 3vh;
	cursor: pointer;
	.bar1 {
		transform: translate(0, 11px) rotate(-45deg);
	}
	.bar2 {
		opacity: 0;
	}
	.bar3 {
		transform: translate(0, -11px) rotate(45deg);
	}
}
